       url('mandalore.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'mandalore_laserregular';
  src: url('mandalorelaser.woff2') format('woff2'),
       url('mandalorelaser.woff') format('woff');
